html,
#root,
body {
  height: 100%;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

h2 {
  margin: 0;
  padding: 22px 0 0 24px;
  font-family: Formular, sans-serif;
  font-size: 24px;
  font-weight: 500;
}

.widget-creation {
  margin-top: 15px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;

  & button {
    display: flex;
    align-items: center;
    width: 44%;
    margin: 4px 0;
    cursor: pointer;
    text-align: left;
    background-color: rgba(66, 98, 255, 1);
    border: none;
    font-family: OpenSans, Arial, Helvetica, sans-serif;
    color: white;
    padding: .4rem;
    font-size: 12px;
    font-weight: 600;
    border-radius: 4px;

    &:hover {
      background-color: rgba(69, 91, 237, 1);
    }

    & > svg {
      width: 14px;
      height: auto;
      fill: white;
      margin-right: 4px;
    }
  }
}

.group-wrapper {
  border: 1px solid rgba(95, 92, 128, 1);
  border-radius: 8px;
  padding: 10px;
  font-family: OpenSans, Arial, Helvetica, sans-serif;
  color: #050038;

  h5 {
    margin: 0;
  }

  .metadata-value {
    padding: 10px;

    span {
      text-align: center;
      padding: 6px;
      margin-left: 15px;
      border-radius: 15px;
      background-color: gray;
    }
  }

  .widgets-label {
    font-weight: 600;
  }
}

